<template>
    <div>
        <div class="field-part">
            <mt-cell title="" :value=" dataForm.name "></mt-cell>
            <mt-cell title="" :value="dataForm.line"> </mt-cell>
            <mt-cell title="" value="90954.95"></mt-cell>
            <mt-cell title="" value="20190627"></mt-cell>
