            <mt-cell title="" value="300000"></mt-cell>
            <mt-cell title="" value="300000"></mt-cell>
            <mt-cell title="" value="300000"></mt-cell>
            <mt-cell title="" value="8468.56"></mt-cell>
            <mt-cell title="" value=""></mt-cell>
            <mt-cell title="" value="20171108"></mt-cell>
            <mt-cell title="" value="02"></mt-cell>
        </div>
        <div class="field-action">
            <mt-button type="default"  @click="$back()"></mt-button>
        </div>
    </div>


</template>

<script>
    export default {
        data (){
            return {
                dataForm: {
                    name: '',
                    line: '',
                    account: ''
                },
            }
        },
        created() {
            this.dataForm = Object.assign({}, this.dataForm, this.$route.params.info );
            if("625228***0089"==this.dataForm.account) {
                this.dataForm.name="";
                this.dataForm.line="20000";
            }else if ("625228***0025"==this.dataForm.account){
                this.dataForm.name="2";
                this.dataForm.line="30000";
            }else {
                this.dataForm.name="3";
                this.dataForm.line="40000";
            }
        }

    }
</script>

<style lang="scss" scoped>

    @import '../../assets/styles/mixins';

</style>



